.fade-in-contact {
  animation: fade-in-contact 1s ease-in forwards;
  z-index: 5;
  border: 2px solid rgb(25, 0, 255);
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5rem 0rem 0 0rem ;
}

@keyframes fade-in-contact {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.contact-form {
  /* position: relative;
  display: block; */
  align-content: center;
  /* top: 30%;
  left: 155%; */
  padding: 25px;
  background-color: #2e324aa1;
  border: 1.5px solid #4866ff;
  border-radius: 3px;
  box-shadow: 0 0px 20px #001fbe;

  
}

.light-theme .contact a {
  color: #0a0a0a;
  font-weight: 500;
}

.dark-theme .contact a {
  color: #f5f5f5;
}

.input-container {
  /* position: absolute;
  top: 40.5px;
  left: 50px; */
  
}

.mail {
  width: 145px;
}

.phone {
  /* position: absolute;
  top: 26.75%;
  left: 53%; */
  width: 145px;
}

.message {
  height: 100px;
}

textarea {
  width: 306.5px;
  height: 100px;
  border-radius: 3.5px;
  border: none;
  margin-bottom: 25px;
  font-family: Poppins;
  font-size: 12.5px;
  padding-left: 5px;
  resize: none;
}

.h1 {
  font-family: "Script MT";
  font-size: 30px;
  /* position: absolute;
  top: 29px;
  left: 10%; */
  z-index: 5;
  text-align: center;
}

.h2 {
  font-family: "Script MT";
  font-size: 25px;
  /* position: absolute;
  top: 47%;
  left: 20%; */
  text-align: center;
  z-index: 5;
}

.social-media a {
  font-size: 10px;
  text-decoration: none;
  text-align: center;
  z-index: 5;
  animation: slide-up 1s ease-in-out;
}

@keyframes slide-up{
  from {
    top: 100%;
  }
  to {
    top: 57%;
  }
}

.light-theme .social-media a {
  color: #0a0a0a;
  font-weight: 500;
}

.dark-theme .social-media a {
  color: #f5f5f5;
}

